import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Link from "@mui/material/Link";

const dispatchedEvents = [];

const SrsMuiLink = function (props, ref) {
  return (
    <Link
      href={props.href}
      color={props.color}
      underline={props.underline}
      target={props.target}
      rel={props.rel}
      onClick={props.onClick}
      theme={props.muiTheme}
    >
      <slot />
    </Link>
  );
};

const styles = `
  /* Custom styles for the MuiLink component */
`;

const props = [
  {
    name: "href",
    defaultValue: "",
    type: "string",
  },
  {
    name: "color",
    defaultValue: "inherit",
    type: "string",
  },
  {
    name: "underline",
    defaultValue: "none",
    type: "string",
  },
  {
    name: "target",
    defaultValue: "",
    type: "string",
  },
  {
    name: "rel",
    defaultValue: "",
    type: "string",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiLink),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
